<template>
  <div class="warper" style="width: 100%">
    <el-card shadow="never">
      <el-row>
        <el-col :span="22">
          <div class="grid-content bg-purple">
            <span>商家服务/商家资料</span>
          </div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content bg-purple-light">
            <el-button round>提交修改</el-button>
          </div>
        </el-col>
      </el-row>
    </el-card>

    <el-card class="bold" shadow="never">
      <span>上次修改日期</span> 2020-08-04 9：21：12
      <span>当前审核状态</span>
    </el-card>

    <section class="merchant_content">
      <el-card shadow="never">
        <div class="company_message">
          <p>公司基本信息</p>

          <el-form label-width="130px" class="form_group">
            <el-form-item label="公司名称">
              <el-input placeholder="山东利生集团有限公司" v-model="companyName" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="公司所在地">
              <el-select v-model="region" placeholder="山东省/菏泽市">
                <el-option label="山东省/菏泽市" value="shanghai"></el-option>
                <el-option label="山东省/菏泽市" value="shanghai"></el-option>
                <el-option label="山东省/菏泽市" value="shanghai"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="公司详细地址">
              <el-input placeholder="**路口" v-model="address"></el-input>
            </el-form-item>
            <el-form-item label="公司紧急联系人">
              <el-input placeholder="高志国" v-model="contacts"></el-input>
            </el-form-item>
            <el-form-item label="公司紧急联系人手机">
              <el-input placeholder="12345678901" v-model="phone"></el-input>
            </el-form-item>
            <el-form-item label="公司经营类目">
              <el-select v-model="category" placeholder="选择经营类目">
                <el-option label="选择经营类目" value="shanghai"></el-option>
                <el-option label="选择经营类目" value="shanghai"></el-option>
                <el-option label="选择经营类目" value="shanghai"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>

        <div class="company_message">
          <p>公司基本信息</p>

          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="no" label="序号" width="180"></el-table-column>
            <el-table-column prop="primary" label="一级类目" width="180"></el-table-column>
            <el-table-column prop="secondary" label="二级类目"></el-table-column>
            <el-table-column prop="operate" label="操作">
              <el-button type="danger" size="mini">删除</el-button>
            </el-table-column>
          </el-table>
        </div>
      </el-card>

      <el-card shadow="never">
        <div class="company_message">
          <p>营业执照信息</p>

          <el-form label-width="130px" class="form_group">
            <el-form-item label="公司名称">
              <el-input placeholder="山东利生集团有限公司" v-model="companyName" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="营业执照注册号">
              <el-input placeholder="11111111" v-model="contacts"></el-input>
            </el-form-item>
            <el-form-item label="营业执照所在地">
              <el-input placeholder="山东的/菏泽市/成武县" v-model="phone"></el-input>
            </el-form-item>
            <el-form-item label="营业执照详细地址">
              <el-input placeholder="**路口" v-model="address"></el-input>
            </el-form-item>
            <el-form-item label="营业执照详细地址">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: "page",
  data() {
    return {
      companyName: "", // 公司名称
      region: "",
      address: "",
      contacts: "",
      phone: "",
      category: "",
      tableData: [
        {
          no: 1,
          primary: "面粉",
          secondary: "普通粉",
        },
        {
          no: 2,
          primary: "面粉",
          secondary: "专用粉",
        },
      ],
      imageUrl: "", // 上传图片
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
  },
  components: {},
};
</script>

<style lang="scss" ref="stylesheet/scss">
.warper {
  font-size: 15px;
}
.form_group {
  width: 600px;
  margin: 0 auto;
}
.bold span {
  font-weight: bold;
}

.merchant_content {
  height: 69vh;
  overflow-x: hidden;
	overflow-y: scroll; 

  &::-webkit-scrollbar{
    display: none;
  }
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.bg-purple{
  line-height: 40px;
}
</style>
